<template>
  <div id="topicReply" style="width:100%;overflow-x:hidden">
    <div class="reply">
      <div class="PostsDataList" >
        <dl>
          <dt>
            <img v-bind:src="reply.headurl" alt="" id="head-img" class="imgs">
            <p class="UseName">{{reply.tusername}}</p>
            <span class="PostsDataTime">{{reply.replytime}}</span>
          </dt>
          <dd class="floor">第{{reply.replyNum}}楼</dd>
        </dl>
        <div class="PostsDataCen">
          <p class="content">{{reply.replycontent}}</p>
          <!--<p class="topic-img">-->
            <!--<img src="./img/11.jpeg" alt="">-->
          <!--</p>-->
          <div class="PostsAction">
            <span><img src="./img/no_zan.png">{{reply.replyIspraise}}</span>
            <span><img src="./img/replay.png">{{reply.replyNum}}</span>
          </div>
          <div class="PostsReply">
            <div v-for="item in replylist">
              <p><strong>{{item.husername}}&nbsp:&nbsp<span>{{item.replycontent}}</span></strong><strong class="PostsReplyTime">{{item.replytime}}</strong></p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <replyInput></replyInput>
  </div>
</template>

<script>
// http://chenggua.com/api.php?r=topic/selreply&token=9859f71e3d0a3420cff434b621b37513&titleid=47676&
// replyid=254340&userid=100035719&pagenum=1
  import replyInput from '../replyInput/replyInput'
  export default {
    name: '',
    data () {
      return {
        token:'9859f71e3d0a3420cff434b621b37513',
        titleid: '',
        replyid: '',
        userid: '100035719',
        pagenum: 1,
        replylist: [],
        reply: ''
      }
    },
    created() {
      // this.$axios.get('http://chenggua.com/api.php?r=topic/selreply&userid='
      // + this.userid+'&token='+ this.token+'&titleid='+ this.titleid+'&replyid='+ this.replyid +'&pagenum='+ this.pagenum).then((response) => {
      //   this.replylist = response.reply.result
      //   console.log(this.replylist)
      // })
      console.log(this.$route.params)
      this.titleid = this.$route.params.titleid
      this.replyid = this.$route.params.replyid
       this.$axios.get('http://chenggua.com/api.php?r=topic/selreply&userid='
       + this.userid+'&token='+ this.token+'&titleid='+ this.titleid+'&replyid='+ this.replyid +'&pagenum='+ this.pagenum).then((response) => {
      //  this.replyt = reponse.data.reply
      this.reply = response.data.reply
      this.replylist = response.data.reply.result
      // console.log(this.replylist)
      console.log(this.reply)

        
      },(error) => {
        // error
        console.log(error)
      });
        
    },
    components: {
      'replyInput': replyInput,
    }
  }
</script>

