<template>
    <div id="allCommunity">
        <topTopic :topTopicList="topTopicList"></topTopic>
        <topic :topicList="topicList" :communityid="communityid"></topic>
    </div>
</template>
<script>
    import topTopic from '../topTopic/topTopic'
    import topic from '../topic/topic'
    export default {
        data () {
            return {
                pagenum: 1,
                communityid: 10096,
                token: '196890753d7cb1cfe3eb37b324ce428a',
                userid: '100044365',
                topicList: [],
                topTopicList: [],
            }
        },
        created (){
            this.$axios.get('http://chenggua.com/api.php?r=topic/gettopiclist&userid='+this.userid+'&communityid='+this.communityid+'&pagenum='+this.pagenum+'&token='+this.token).then((response) => {
            let self = this;
            response.data.result.map(function(val, index){
                if (val.titleIstop == 1){
                    self.topTopicList.push(val)
                }else{
                    self.topicList.push(val)
                }
            })
            },(error) => {
                // error
                console.log(error)
            });
        },
        methods: {},
        components: {
            'topTopic': topTopic,
            'topic': topic,
        }
    }
</script>

<style rel="stylesheet/scss" lang="sass" scoped>

</style>



