<template>
    <div id="toast">
        <div class="toast">
            <div>{{ toast }}</div>
        </div>
    </div>
</template>

<script>
    export default {
        name: '',
        props: [
          'toast'
        ],
        data () {
            return {}
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

  .toast {
    width: 18rem;
    height: 4rem;
      display: table;
      position: fixed;
      padding: .5rem 1rem;
    background-color: #f2f2f6;
    border-radius: 0.6rem;
    filter:alpha(opacity: 90);
    opacity: 0.9;
    margin: auto;
    /*top: 16rem;*/
    right:0;
    left: 0;
  }
  .toast div {
      display: table-cell;
      word-break: break-all;
      vertical-align: middle;
    font-size: 1.3rem;
    color: #28283b;
    text-align: center;
    letter-spacing: 0.1rem;

  }
</style>



