import axios from 'axios'
import * as dataType from '../guaType'
const topicModule= {
    state: {
        orderby: 0,
        topicCen: ''
    },

    getters: {
        [dataType.TOPIC_GETTOPIC] : ( state ) => {
          return state.topicCen.topic
        },
        [dataType.TOPIC_GETREPLY] : ( state )  =>  {
            return state.topicCen.reply
        }
    },

    actions: {
        [dataType.TOPIC_CEN]({commit ,state, rootState}, topic) {
            axios.get('http://api.chenggua.com/api.php?r=topic/insidepages&userid='+rootState.userModule.userData.userid+'&titleid='+topic.titleid+'&communityid='+topic.communityid+'&orderby='+state.orderby+'&token='+rootState.userModule.userData.token).then((response) => {
                // success
                let topicCen = new Object()
                topicCen.topic = response.data.result.topic
                topicCen.reply = response.data.result.reply
                let context = response.data.result.topic.topiccontent;
                let images = response.data.result.topic.topicImg;
                let conImg = [];
                if(context.indexOf('name="imageName"') !== -1){

                }else{
                    for(var i=0;i<images.length;i++){
                        conImg.push({imgUrl: images[i].imgUrl, imgwidth: images[i].imgwidth+'px', imgheight: images[i].imgheight+'px'})
                    }
                    topicCen.ContentImg = conImg
                }
                commit(dataType.TOPIC_CEN, topicCen)
            },(error) => {
                // error
                console.log(error)
            });
        },
    },

    mutations: {
        [dataType.TOPIC_CEN](state, data) {
            state.topicCen = data
        },
    }
}
export default topicModule



