import axios from 'axios'
import * as dataType from '../guaType'
const userModule= {
    state: {
        userData: {token: '196890753d7cb1cfe3eb37b324ce428a', userid: '100044365'},
        userFrom: {
            loginType: false,
            loginCoode: ''
        }
    },

    getters: {
        [dataType.USER_GETDATA]: ( state ) => {
            return state.userData
        },
        [dataType.USER_GETTYPE]: ( state ) => {
            return state.userFrom.loginType
        },
        [dataType.USER_GETCOODE]: ( state ) => state.userFrom.loginCoode
    },

    actions: {
        [dataType.USER_SIGNIN]({commit}, userCen) {
            let api = 'http://api.chenggua.com/api.php?r=user/login';
            axios.post(api, JSON.stringify({loginName: userCen.userTel, password: userCen.userPwd})).then((response) => {
                // success
                if(response.data.status === 200 ){
                    let userCon = {
                        userid: response.data.userid,
                        token: response.data.token,
                        coode: 200
                    };
                    commit(dataType.USER_SIGNIN, userCon)
                }else{
                  let userCon = {
                    coode: response.data.status
                  };
                  commit(dataType.USER_SIGNIN, userCon)
                }
            },(error) => {
                // error
                console.log(error)
            });
        },
        [dataType.USER_SIGOUT]({commit}) {
            commit(dataType.USER_SIGOUT)
        }
    },

    mutations: {
        [dataType.USER_SIGNIN](state, userCen) {
            if (userCen.coode == 200) {
                state.userFrom.loginType = true;
                state.userFrom.loginCoode = 200;
                state.userData = {
                    userid: userCen.userid,
                    token: userCen.token,
                }
            }else {
              state.userFrom.loginType = false;
              state.userFrom.loginCoode = userCen.coode;
            }
        },
        [dataType.USER_SIGOUT](state) {

        }
    }
}
export default userModule



