<template>
  <transition name="">
    <div id="success">
      <div class="successShade"></div>
      <div class="animation"></div>
      <div class="success">
        <div class="close">
          <i></i>
        </div>
        <div class="cartoon">
          <div>
            <span>恭喜~</span>
          </div>
        </div>
        <div class="msg">
          <div></div>
          <span>为社团获得了53点热度, <br>社团排名又提升了</span>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  export default {
    name: '',
    data () {
      return {}
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .successShade {
    width: 100%;
    height: 100%;
    background-color: #000;
    filter:alpha(opacity:80);
    opacity: 0.8;
    position: fixed;
    left: 0;
    top: 0;
  }
  .animation {
    width: 23.427rem;
    height: 23.427rem;
    position: fixed;
    margin: auto;
    top: 0;
    right:0;
    left: 0;
    bottom: 0;
    background-image: url("./img/c_drawable-hdpi/light_normal_1x.png");
    /*filter:"progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod='scale')";*/
    /*-moz-background-size:100% 100%;*/
    background-size: cover;
    -webkit-animation:circle 3s infinite linear;
  }
  @-webkit-keyframes circle {
    0% { transform:rotate(0deg); }
    100%{ transform:rotate(-360deg); }
  }
  .success {
    width: 23.427rem;
    height: 23.427rem;
    position: fixed;
    margin: auto;
    top: 0;
    right:0;
    left: 0;
    bottom: 0;
  }
  .success .close {
    width: 100%;
    height: 3rem;
  }
  .success .close i {
    float: right;
    display: inline-block;
    width: 3rem;
    height: 3rem;
    margin-right: 4rem;
    background-image: url("./img/c_drawable-hdpi/close_normal_1x.png");
    background-size: cover;
  }
  .success .cartoon {
    margin-bottom: 0.2rem;
  }
  .success .cartoon div {
    position: relative;
    margin: 0 auto;
    width: 16rem;
    height: 16rem;
    background-image: url("./img/c_drawable-hdpi/chargeflish_normal_1x.png");
    background-size: cover;
  }
  .success .cartoon span {
    position: absolute;
    font-size: 1.2rem;
    letter-spacing: 1px;
    color: #fff;
    left: 6.6rem;
    bottom: 0;

  }
  .success .msg {
  }
  .success .msg div {
    position: absolute;
    width: 17rem;
    height: 5rem;
    margin: 0 3rem;
    background-image: url("./img/c_drawable-hdpi/textbg_normal_1x.png");
    background-size: cover;
  }
  .success .msg span {
    position: absolute;
    display: block;
    margin: 1rem 4.8rem;
    font-size: 1.1rem;
    letter-spacing: 2px;
    line-height: 1.73rem;
    color: #fff;
  }

</style>



