        }else if(short_header == '名人'){
          short_header_img = "http://www.chenggua.com/html5/share/images/celebrity.png"
        }else if(short_header == '编辑'){
          short_header_img = "http://www.chenggua.com/html5/share/images/editor.png"
        }else if(short_header == '总编'){
          short_header_img = "http://www.chenggua.com/html5/share/images/editorAll.png"
        }else if(short_header == '主编'){
          short_header_img = "http://www.chenggua.com/html5/share/images/editors.png"
        }else if(short_header == '媒体人'){
          short_header_img = "http://www.chenggua.com/html5/share/images/journalists.png"
        }else if(short_header == '官方'){
          short_header_img = "http://www.chenggua.com/html5/share/images/official.png"
        }else if(short_header == '作家'){
          short_header_img = "http://www.chenggua.com/html5/share/images/writer.png"
        }else{
          short_header_img = "";
        }
        return short_header_img
      },
      wealthGrade: function (header) {
        var wealthGrade = header -1; //财富值
        var wealthGrade_img = "";
        if(wealthGrade == "9"){
          wealthGrade_img  = "http://www.chenggua.com/html5/share/images/wealthGrade_9.png"
        }else if(wealthGrade == "8"){
          wealthGrade_img  = "http://www.chenggua.com/html5/share/images/wealthGrade_8.png"
        }else if(wealthGrade == "7"){
