<template>
  <div id="community">
    <top-prizeBanner></top-prizeBanner>
    <div class="community flex_between">
      <div class="community-img">
        <img :src="communitydetails.communitylogn" alt="">
      </div>
      <div class="community-msg">
        <h2>{{communitydetails.communityname}}</h2>
        <h3>会长:&nbsp{{communitydetails.username}}</h3>
        <p>帖子:&nbsp{{topicNumber}}&nbsp&nbsp粉丝:{{number}}</p>

        <div class="progressBar-container" v-show="communitydetails.isJoin==1 ? true : false" v-link="">
          <div class="progressBar">
          </div>
          <span id="recharge">充能</span>
        </div>
        <b class="concern" v-show="communitydetails.isJoin==1 ? false : true">+关注</b>
      </div>
    </div>
    <div class="list">
      <ul>
        <li>
          <router-link :to="{ path: '/Community/allCommunity' }">全部帖子</router-link>
          <b></b>
        </li>
        <li>
          <router-link :to="{ path: '/Community/topic' }">会长动态</router-link>
          <b></b>
        </li>
        <li>
          <router-link :to="{ path: '/myPage/myPageTopic' }">我的更多</router-link>
          <b></b>
        </li>
      </ul>
    </div>
    <router-view>
    </router-view>
    <!--<sign></sign>-->
    <!--<success></success>-->
  </div>

</template>

<script>
  import prizeBanner from '../prizeBanner/prizeBanner'
  import Sign from '../Sign/Sign'
  import Success from '../Success/Success'
  export default {
    name: '',
    data () {
      return {
        pagenum: 1,
        communityid: 10096,
        token: '196890753d7cb1cfe3eb37b324ce428a',
        userid: '100044365',
        communitydetails:'',
        number:0,
        topicNumber:'',
        aa: '',
      }
    },
    mounted () {
      this.$emit('view', this.meta())
    },
    preFetch () {
      return this.methods.meta()
    },
    methods: {
      meta () {
        return {
          title: '天府社团',
          description: '橙瓜社团,天府社团',
          keywords: '橙瓜社团'
        }
      }
    },
    created() {
      this.$axios.get('http://chenggua.com/api.php?r=community/communityhomepage&userid='
      +this.userid+'&communityid='+this.communityid+'&pagenum='+this.pagenum+'&token='+this.token).then((response) => {
    
        this.communitydetails = response.data.result
        this.number = response.data.result.number
        this.topicNumber = response.data.result.topicNumber
        if (this.number>10000) {
          this.number = parseInt(this.number/10000) + '万'
        }
        if (this.topicNumber>10000) {
          this.topicNumber = parseInt(this.topicNumber/10000) + '万'
        }
      },(error) => {
        // error
        console.log(error)
      });
    },
    components: {
      'top-prizeBanner': prizeBanner,
      'sign': Sign,
      'success': Success
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #community {
    background-color: #f8f8f8;
  }
  .community {
    padding: 10px;
    background-color: #fff;
    margin-bottom: 0.1rem;
  }
  .community .community-img {
    width: 12.5rem;
    height: 7.4rem;
  }
  .community img{
    width: 12.5rem;
    height: 7.4rem;
  }
  .community .community-msg {
    width: 100%;
    padding: 5px 11px;
    color: #94949e;
    text-align: left;
  }
  .community .community-msg h2 {
    color: #1f1f1f;
    font-size: 1.6rem;
    letter-spacing: 2px;
    margin-bottom: 0.45rem;
    overflow: hidden;
  }
  .community .community-msg h3 {
    height: 1.25rem;
    color: #28283b;
    font-size: 1.25rem;
    letter-spacing: 2px;
    margin-bottom: 0.45rem;
  }
  .community .community-msg p {
    font-size: 1.14rem;
    letter-spacing: 2px;
  }
  .community .community-msg b {
    display: inline-block;
    width: 4.5rem;
    height: 1.8rem;
    margin-top: 3px;
    margin-right: 0.7rem;
    border-radius: .2rem;
    background-color: #ff8e42;
    color: #fff;
    font-size: 1.14rem;
    letter-spacing: 2px;
    text-align: center;
    line-height: 1.8rem;
  }
  .progressBar-container {
    position: relative ;
  }
  .community .community-msg .progressBar {
    display: inline-block;
    position: relative;
    width: 5rem;
    height: 1.8rem;
    margin-top: 3px;
    margin-right: 0.7rem;
    border-radius: 0.2rem;
    background-image:url("./img/c_drawable-hdpi/progressbg_normal_1x.png");
    background-image: -webkit-image-set(url(img/c_drawable-hdpi/progressbg_normal_1x.png)
    1x,url(img/c_drawable-xxhdpi/progressbg_normal_2x.png)
    2x);
    color: #fff;
    font-size: 1.14rem;
  }
  .community .community-msg .progressBar div {
    display: block;
    position: relative;
    background-image:url("./img/c_drawable-hdpi/rechargetexture_normal_1x.png");
    background-image: -webkit-image-set(url(img/c_drawable-hdpi/rechargetexture_normal_1x.png)
    1x,url(img/c_drawable-xxhdpi/rechargetexture_normal_2x.png)
    2x);
    width: 70%;
    height: 1.8rem;
    border-radius: 0.2rem;
  }
  #recharge {
    display: inline-block;
    color: #fff;
    font-size: 1rem;
    letter-spacing: 3px;
    position: absolute;
    left: 1.25rem;
    top: 0.58rem;
  }
  .list {
    background-color: #fff;
    margin-bottom: 0.2rem;
  }
  .list ul {
    width: 100%;
    height: 100%;
    display: flex;
    display: -webkit-flex;
    align-items: stretch;
    -webkit-align-items: stretch;
  }
  .list ul li {
    flex-grow: 1;
    position: relative;
    text-align: center;
  }
  .list ul li a {
    width: 100%;
    height: 100%;
    display: block;
    font-size: 1.4rem;
    color: #28283b;
    line-height: 3.14rem;
  }
  .list ul li b {
    width: 5rem;
    height: 1px;
    background-color: #ff8e42;
    display: inline-block;
    position: absolute;
    bottom: 0;
    left: 2.8rem;
  }

</style>



