<template>
  <div class="content-top" v-if="topTopicList.length != 0">
    <div class="top">
      <ul class="">
        <li class="masNLen flex_align" :class="{ 'weui_grid' : index != topTopicList.length-1 }" v-for="(item, index) in topTopicList" @click="toReply(item.titleid)">
          <b class="flex_align">置顶</b><strong class="maxNLen">{{ item.title }}</strong>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    props: [
      'topTopicList',
      "communityid"
    ],
    name: '',
    data () {
      return {}
    },
    methods: {
      toReply: function (titleid) {
        this.$parent.goReply(titleid)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .content-top {
    width: 100%;
    padding-top: 0.34rem;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    background-color: #f2f2f6;
  }
  .content-top .top {
    background-color: #fff;
    padding: 0 0.57rem;
  }
  .content-top .top li{
    height: 3.14rem;
    font-size: 1.43rem;
    color: #000;
    line-height: 3.14rem;
  }
  .content-top .top li.weui_grid:before,.content-top .top li.weui_grid:after{
    border-bottom: 1px solid #e9e9eb;
    width: 100%;
    bottom: 0;
    left: 0;
  }
  .content-top .top li.weui_grid:before{
    left: -50%;
  }
  .content-top .top li b {
    width: 3rem;
    height: 1.5rem;
    /*display: inline-block;*/
    /*text-align: center;*/
    /*padding: .25rem 0;*/
    border: 1px solid #f7af30;
    /*padding: 2px 5px;*/
    margin-right: 6px;
    /*vertical-align: middle;*/
    border-radius: 4px;
    font-size: .9rem;
    line-height: .9rem;
    color: #f7af30;
    justify-content: center;
    -webkit-justify-content: center;
  }
</style>



