<template>
  <div id="topicReply" style="width:100%;overflow-x:hidden">
    <div class="reply" v-for="item in reply">
      <div class="PostsDataList" >
        <dl>
          <dt>
            <img v-bind:src="item.headurl" id="head-img" class="imgs">
            <p class="UseName">{{item.tusername}}</p>
            <span class="PostsDataTime">{{item.replytime}}</span>
          </dt>
          <dd class="floor">第{{item.replaylouce}}楼</dd>
        </dl>
        <div class="PostsDataCen">
          <p class="content">{{item.replycontent}}</p>
          <!--<p class="topic-img">
            <img src="./img/11.jpeg" alt="">
          </p>-->
            <div class="PostsReply" v-for="item in item.result">
              <p><strong>{{item.husername}} &nbsp: <span> &nbsp {{item.replycontent}}</span></strong><strong class="PostsReplyTime">{{item.replytime}}</strong></p>
            </div>
          <div class="PostsAction">
            <span><img src="./img/no_zan.png">{{item.replyPraiseNum}}</span>
            <router-link :to="'/reply/replyToReply/'+item.titleid+'/'+item.replyid" tag="span"><img src="./img/replay.png">{{item.replyNum}}</router-link>
            <span>...</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import MugenScroll from 'vue-mugen-scroll'

  export default {
    props: [
      'reply'
    ],
    name: '',
    data () {
      return {
      }
    },
    watch: {

    },
    methods: {

    },
    components: {
      MugenScroll
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .imgs{
    border-radius: 50%;
    -webkit-border-radius:50%;
    border: 1px solid #a9205b;
  }
  .reply{
    width: 100%;
    padding:1rem 0.5rem 0 0.5rem ;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
  }
  .reply .PostsDataList dl {
    height: 100%;
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    align-items: center;
    -webkit-align-items:center;
  }
  .reply .PostsDataList dt .UseName img {
    height: 1rem;
    margin-left: .6rem;
  }
  .reply .PostsDataList{
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    padding: 0 0 3rem 0.5rem;
    /*border-top: 1px solid #2b2b3d;*/
    border-bottom: 1px solid #e9e9eb;
  }
  .reply .PostsDataList dd {
    color: #282838;
    font-size: 1.14rem;
  }
  .reply .PostsDataList .PostsDataTime,.PostsDataList .PostsDataCen .PostsReply .PostsReplyTime {
    color: #94949e;
    font-size: 1rem;
  }
  .reply .PostsDataList dl{
    margin-top: 1.35rem;
  }
  .reply .PostsDataList dt{
    padding-left: 4rem;
    position: relative;
    line-height: 1.5rem;
  }
  .reply .PostsDataList dt>img{
    width: 3rem;
    height: 3rem;
    position: absolute;
    top:0;
    left: 0;
  }
  .reply .PostsDataList .PostsDataCen .PostsReply .PostsReplyTime{
    margin-left: 1rem;
  }
  .reply .PostsDataList .PostsDataCen{
    padding: 0 0 0 4rem;
    margin-top: .85rem;
    font-size: 2rem;
  }
   .PostsDataList .PostsDataCen p{
    /*font-size: 1.1rem;*/
    line-height: 2rem;
    font-size: 1.3rem;
  }
  .PostsDataCen>p,p.UseName,.replay-list .PostsDataList dd,.PostsReply>p>b{
    font-weight: 400;
    color: #28283B;
  }
  .replay-list .PostsDataList dd{
    margin-right: 0.9rem;
  }
  span.PostsDataTime,.PostsDataList dd span.PostsDataTime,.PostsDataList .PostsAction,.FloorNum,.PlayTour .Message{
    color: #94949e;
  }
  .PostsDataList .PostsDataCen .PostsReply{
    font-size: .9rem;
    margin-top: 2.1rem;
    padding-top: 1.1rem;
    /*font-size: .9rem;*/
    /*margin-top: 1.5rem;*/
    /*border-top: 1px solid #e9e9eb;*/
  }
  .reply .content{
    font-size: 1.2rem;
  }
  .reply .PostsDataList .PostsDataCen .PostsReply{
    border-top: 1px solid #e9e9eb;
  }
  .PostsDataList .PostsDataCen .PostsReply p{
    font-size: .9rem;
    /*padding-bottom: .8rem;*/
  }
  .PostsDataList .PostsAction {
    /*width: 9.8rem;*/
    /*width: 6rem;*/
    float: right;
    /*text-align: right;*/
    display: flex;
    display: -webkit-flex;
    justify-content: space-around;
    -webkit-justify-content: space-around;
    align-items: center;
    -webkit-align-items: center;

  }
  .PostsDataList .PostsAction span {

  }
  .PostsDataList .PostsAction span img{
    width: 1rem;
    margin-right: .35rem;
  }
  .PostsDataList .PostsAction span{
    letter-spacing: 0.1rem;
    margin: 0 0.5rem;
    font-size: 1.026rem;
    /*display: flex;*/
    /*display: -webkit-flex;*/
    /*align-items: center;*/
    /*-webkit-align-items: center;*/
  }
  .content{
    word-break: break-all;
    font-size: 1.5rem ;
  }
  .content p img{
    width: 100%;
  }
  .reply .content{
    font-size: 1.2rem;
  }
  .reply .PostsDataList .PostsAction{
    margin-top: 1rem;
  }
  .reply .PostsDataList .PostsDataCen .PostsReply{
    margin-top: 3.1rem;
    padding-top: 1.6rem;
  }
  .PostsDataCen p{
    font-size: 1.1rem;
    line-height: 1.6rem;
    /*padding-bottom: 1rem;*/
  }
  .PostsDataCen p img{
    width: 100%
  }




</style>



