<template>
<div id="homePage">
  <prizeBanner></prizeBanner>
  <search></search>
  <top-topic :topTopicList="getList.topTopicList" :communityid="communityid"></top-topic>
  <topic :topicList="getList.topicList" :communityid="communityid"></topic>
  <publishTopic></publishTopic>
</div>
</template>

<script>
  import prizeBanner from '../prizeBanner/prizeBanner'
  import search from '../Search/search'
  import topTopic from '../topTopic/topTopic'
  import topic from '../topic/topic'
  import publishTopic from '../publishTopic/publishTopic'
  import { mapActions, mapGetters, mapState, mapMutations } from 'vuex'
  export default {
    data () {
      return {
        pagenum: 1,
        communityid: 10007,
        token: '196890753d7cb1cfe3eb37b324ce428a',
        userid: '100044365',
        topicList: [],
        topTopicList: [],
      }
    },
    mounted () {
      this.$emit('view', this.meta())
    },
    preFetch () {
      return this.methods.meta()
    },
    methods: {
      meta () {
        return {
          title: '橙瓜首页',
          description: '这是橙瓜首页',
          keywords: '橙瓜'
        }
      },
      goReply: function (titleid) {
//        window.location.href="reply/topicShow/"+titleid+"/"+this.communityid
        this.$router.push("reply/topicShow/"+titleid+"/"+this.communityid)
      },
      ...mapActions([
        'aa'
      ]),
      ...mapMutations([
        'BEGIN_CLEAR'
      ])
    },
    computed: {
      ...mapState([
        'comList'
      ]),
      ...mapGetters([
        'getList'
      ])
    },
    created() {
      this.BEGIN_CLEAR()
      this.aa()
      this.topicList = this.getList.topicList
      this.topTopicList = this.getList.topTopicList
    },
    components: {
      'prizeBanner': prizeBanner,
      'search': search,
      'topTopic': topTopic,
      'topic': topic,
      'publishTopic': publishTopic
    }
  }
</script>

