import Vue from 'vue'
import Router from 'vue-router'

import homePage from '../components/homePage/homePage'
import myPage from '../components/myPage/myPage'
import myPageTopic from '../components/myPageTopic/myPageTopic'
import myPageCommunity from '../components/myPageCommunity/myPageCommunity'
import myPageReply from '../components/myPageReply/myPageReply'
import recharge from '../components/Recharge/Recharge'
import Recording from '../components/Recording/Recording'
import Community from '../components/Community/Community'
import allCommunity from '../components/allCommunity/allCommunity'
import topic from '../components/topic/topic'
import Login from '../components/Login/Login'
import sendTopic from '../components/sendTopic/sendTopic'
import SearchResult from '../components/SearchResult/SearchResult'
import reply from '../components/replyInput/replyInput'
import topicShow from '../components/topicShow/topicShow'
import replyToReply from '../components/replyToReply/replyToReply'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { path: '/', name: 'index', component: homePage },
    { path: '/myPage', component: myPage, children: [
      { path: '/myPage/myPageTopic', name: 'myPageTopic', component: myPageTopic },
      { path: '/myPage/myPageCommunity', name: 'myPageCommunity', component: myPageCommunity },
      { path: '/myPage/myPageReply', name: 'myPageReply', component: myPageReply },
      { path: '/myPage/myPageSave', name: 'myPageSave', component: myPageTopic }
    ]},
    { path: '/Recharge', name: 'Recharge', component: recharge},
    { path: '/Recording', name: 'Recording', component: Recording },
    { path: '/Community', component: Community, children:[
      { path: '/Community/allCommunity', name: 'allCommunity', component: allCommunity },
      { path: '/Community/topic', name: 'topic', component: topic },
    ]},
    { path: '/Login', name: 'Login', component: Login},
    { path: '/sendTopic', name: 'sendTopic', component: sendTopic},
    { path: '/SearchResult', name: 'SearchResult', component: SearchResult},
    {path: '/reply', component: reply, children: [
      { path: '/reply/topicShow/:titleid/:communityid', name: 'topicShow', component: topicShow},
      { path: '/reply/replyToReply/:titleid/:replyid', name: 'replyToReply', component: replyToReply}
    ]},
  ]
})


