<template>
  <div id="login">
    <div class="nav">
      <span class="nav-left" v-on:click="back()">取消</span>
      <span class="nav-center">登录橙瓜</span>
      <span class="nav-right">取消</span>
    </div>
    <hr>
    <div class="content">
      <input type="number" placeholder="请输入手机号" v-model="userTel" class="tel"/>
      <input type="password" placeholder="密码输入" v-model="userPwd" class="pwd"/>
      <input type="button" v-on:click="signin" value="登录" class="content-login"/>
    </div>
    <div class="login-other">其他登录方式</div>
    <div class="foot">
      <div class="login-3">
        <div class="login-3-cell" v-on:click="isWX">
          <div> <img src="./img/weic_2x.png" alt=""></div>
          <div>
            <span>微信</span>
          </div>
        </div>
        <div class="login-3-cell">
          <div> <img src="./img/weiB_2x.png" alt=""></div>
          <div>
            <span>微博</span>
          </div>
        </div>
        <div class="login-3-cell">
          <div> <img src="./img/QQ_2x.png" alt=""></div>
          <div>
            <span>QQ</span>
          </div>
        </div>
      </div>
    </div>
    <transition name="error">
      <toast :toast="toast" v-if="error"></toast>
    </transition>
  </div>
</template>

<script>
  import MD5 from 'js-md5'
  import toast from '../toast/toast'
  import { mapActions, mapGetters } from 'vuex'
  import { isWeiXin } from  '../../js/ToPublic'
  export default {
    name: 'Login',
    data () {
      return {
        userTel: '',
        userPwd: '',
        toast: '',
        error: false
      }
    },
    watch: {
      'USER_GETTYPE': 'signinGo'
    },
    computed: {
      ...mapGetters({
        USER_GETCOODE: 'user/USER_GETCOODE',
        USER_GETTYPE: 'user/USER_GETTYPE'
      }),
    },
    methods: {
      signin (){
        let user = {
          userTel: this.userTel,
          userPwd: MD5(this.userPwd)
        };
        let num =0
        let sfle = this
        if(!user.userTel || !user.userPwd) return
        this.USER_SIGNIN(user)
        this.$store.subscribe((mutation, state) => {
//          console.log(mutation.type)
//          console.log(mutation.payload)
          num+=1
          if(mutation.type == 'user/USER_SIGNIN' && num == 1){
            console.log(this.USER_GETCOODE)
            if(this.USER_GETCOODE == 401){
              this.error=true
              this.toast='账号密码错误!!'
              setTimeout(function(){sfle.error=false},1000)
            }
            else if(this.USER_GETCOODE == 406){
              this.error=true
              this.toast='请输入手机号码!!'
              setTimeout(function(){sfle.error=false},1000)
            }
          }
        })
      },
//      signinGo: function ( val ) {
//        if (val){
//          this.$router.replace("/")
//        }
//      },
      signinGo () {
        this.$router.replace("/")
      },
      isWX (){
        if(isWeiXin()){
          this.$axios.get('http://www.chenggua.com/frontend/web/index.php?r=site/weixin')
          .then((response) => {
            if(!response.data.result){
              window.location.href='http://www.chenggua.com/frontend/web/index.php?r=site%2Fauth&authclient=weixinmp&url=http://192.168.123.189:8888?LOGINT=WX'
            }else {

            }
          },(error) => {
            alert(JSON.stringify(error))
          })
        }else{
          alert('请用微信打开该页面')
        }
      },
      meta () {
        return {
          title: '登录',
          description: 'this is main description',
          keywords: 'view, main'
        }
      },
//      back: function(){
//        this.$router.go(-1)
//      },
//      back (){
//        this.$router.go(-1)
//      },
      ...mapActions({
        USER_SIGNIN: 'user/USER_SIGNIN'
      })
    },
    components: {
      'toast': toast
    },
    mounted () {
      this.$emit('view', this.meta())
    },
    preFetch () {
      return this.methods.meta()
    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #login{
    text-align: center;
  }
  .nav {
    width: 100%;
    margin-bottom: 0.2rem;
  }
  .nav span {
    display: inline-block;
    height: 1.25rem;
    font-size: 1.8rem;
    margin: 0.6rem 0.8rem;
    letter-spacing: 4px;
  }
  .nav-left {
    float: left;
    color: #94949e;
  }
  .nav-center {
    color: #28283b;
  }
  .nav-right {
    float: right;
    color: #ffffff;
  }
  hr {
    height: 0.3rem;
    border: 0;
    margin: 0;
    background: #e9e9eb;
  }
  .content input {
    border-radius: 2px;
    text-indent: 10px;
  }
  .content .tel {
    width: 22rem;
    height: 3.35415rem;
    line-height: 1.421250rem;
    background: #f2f2f6 no-repeat left center;
    background-image: url("./img/drawable-hdpi/phone_1x.png");
    background-image: -webkit-image-set(url(img/drawable-hdpi/phone_1x.png)
    1x,url(img/drawable-xxhdpi/phone_2x.png)
    2x);
    background-position-x: 0.9096rem;
    background-size: 2rem 2rem;
    padding-left: 3.2rem;
    margin-top: 2.5rem;
    margin-bottom: 2rem;
    font-size: 1.421250rem;
  }
  .content .pwd {
    width: 22rem;
    height: 3.35415rem;
    line-height: 1.421250rem;
    padding-left: 3.2rem;
    background: #f2f2f6 no-repeat left center;
    background-image: url("./img/drawable-hdpi/password_1x.png");
    background-image: -webkit-image-set(url(img/drawable-hdpi/password_1x.png)
    1x,url(img/drawable-xxhdpi/password_2x.png)
    2x);
    background-position-x: 0.9096rem;
    background-size: 2rem 2rem;
    font-size: 1.421250rem;
  }
  .content .content-login {
    width: 24.5rem;
    height: 2.7rem;
    line-height: 2.5rem;
    font-size: 1.25rem;
    background: #ff8e42;
    color:#ffffff;
    letter-spacing: 3px;
    margin-top: 3.5rem;
    margin-bottom: 2.5rem;
    appearance:none;
    -webkit-appearance:none;
    -moz-appearance:none;
  }
  /* 3个登录布局 */
  .login-3 {
    display: flex;
    margin: 2rem 7rem;
  }
  .login-3-cell {
    flex: 1;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center
  }

  .login-3 span {
    display: inline-block;
    color: #94949e;
    font-size: 0.8rem;
    margin-top: 0.3rem;
    letter-spacing: 1px;
  }
  img {
    width: 3.5rem;
    height: 3.5rem;
  }
  .login-other{
    padding: 0;
    margin: 1.14rem 2.28rem;
    line-height: 0.25rem;
    letter-spacing: 1px;
    border-left: 7rem solid #ddd;
    border-right: 7rem solid #ddd;
    text-align: center;
  }
  input:-webkit-autofill{
    background: #f2f2f6 no-repeat left center;
  }
  .error-enter-active, .error-leave-active {
    transition: opacity 1.4s
  }
  .error-enter, .error-leave-active {
    opacity: 0
  }
</style>



