import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import axios from 'axios'
import { BEGIN_LIST, BEGIN_CLEAR } from './guaType'
import payModule from './dataType/pay'
import userModule from './dataType/user'
import topicModule from './dataType/topic'
import myPageModule from './dataType/myPage'
import clubPageModule from './dataType/clubPage'

Vue.use(Vuex, axios)

export default new Vuex.Store({
  state: {
    comList: [],
    pagenum: 1,
    load: false
  },

  getters: {
    getList: ( state ) => {
      let sortLIst = {
        topTopicList: [],
        topicList: []
      };
      state.comList.map(function( val ){
        if (val.titleIstop == 1){
          sortLIst.topTopicList.push(val)
        }else{
          sortLIst.topicList.push(val)
        }
      });
      return sortLIst
    }
  },

  actions: {
    aa ({ commit, state ,rootState }) {
      state.load = false;
      axios.get('http://chenggua.com/api.php?r=topic/gettopiclist&userid=100044365&communityid=10007&pagenum='+state.pagenum+'&token=196890753d7cb1cfe3eb37b324ce428a').then((response) => {
        // success
        commit(BEGIN_LIST, response.data.result)
        state.pagenum++
      },(error) => {
        // error
        console.log(error)
      });
    }
  },

  mutations: {
    [BEGIN_LIST] (state, data) {
      state.comList = state.comList.concat(data);
      state.load = true;
    },
    [BEGIN_CLEAR] (state){
      state.comList = []
    }
  },
  modules: {
    payModule,
    userModule,
    topicModule,
    myPageModule,
    clubPageModule,
  },
  plugins: [createPersistedState({
    key: 'user',
    paths: ['userModule']
  })]
})


